*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

body{
    width: 100%;
    height: auto;
}

.container{
    width: 100%;
    height: auto;
}

.header{
    height: 100px;
    width: inherit;
    background-color: rgb(80,167,227);
    color: #fff;
    padding: 4px;
    display:flex;
    flex-direction: row;
justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
}
.header-left {
    width: 40%;
    height: inherit;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
}
.header-left  img{
    height:80px;
    
}

.tagline{
    width: 30% ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    align-items: center;

}

.header-right{
    width: 60%;
}

.header-right > .navbar .nav{
    padding: 0;
    margin: 0;
    list-style: none;
}
.nav{
    width: 100%;
    display: flex;
    justify-content:flex-end;
    
    align-items: center;
    align-content: center;
}
.nav li{

    padding: 15px 25px;
}
.nav li a{
    text-decoration: none;
    color: #fff;
}
.tag-1, .foot-tag-1{
    font-size: 1.4rem;
}
.menu{
    width: 40px;
    height: 40px;
    position: fixed;
    top: 20px;
    z-index: 3;
    right: 35px;
    display: none;
}
.main{
    width: 100%;
    height: auto;
}
.menu > div{ 
    display: block;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    height: 40px;
    margin: auto;

}
.menu > div > i{
    color: red;
}
 .main-1{
    width: 100%;
    min-height: 100vh;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 15px;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.main-1-left{
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    width: 100%;
}
.main-cont-1{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-content: space-around;
    align-items: center;
  
}
.main-list{
    width: fit-content;
    margin: auto;
}

.main-cont-1 h1{
    width: fit-content;
    text-align: left;
}
.main-list li{
    font-size: 1.1rem;
    padding: 7px 14px;
    width: 95%;
    line-height: 1.75;
    margin: auto;
}

.main-2{
    width: 100%;
    min-height: 100vh;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
    justify-content: center;
    align-content: center;
    align-items: center;
    background-color: #f8f8f8;

}

.main-2-left, .main-2-right{
    width: 50vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
}

.main-2-left form{
   width: 75%;
    height: 100%;
    padding: 12px;
    background:rgb(67);
    color: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 30px;
    text-align: left;
    align-content:center;
    align-items: flex-start;
    margin: auto;
}

.main-2-left form input[type="text"],
.main-2-left form label
{
    width: 80%;
    margin: 0px  auto;
    padding: 6px;
}
.main-2-left form input[type="submit"]{
    width: fit-content;
    margin: auto;
    padding: 15px 20px;
}
.main-2-right{
    width: 45vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: space-around;
}
.customer-search{
    width: 100%;
    height: 100%;
}
.customer-search > .users{
    width: 100%;
    height: 100%;
    justify-content: center;
    align-content: center;
    align-items: center;

}
.users h2{
    padding:15px 5px;
}
.users ul{
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: center;
    text-align: left;
    padding-top: 30px;
}
.users ul li{
    width: 60%;
    margin: 0 auto;
    padding: 15px;
    text-align: left;
}

.main-3, .main-2-1{
    display: none;
    width: 100%;
}
.main-3{
    min-height: 100vh;
}
.main-2-1{
    width: 100%;
    margin: auto;
}
.main-3 h2,
.main-2-1 h2{
    text-align: center;
    padding: 10px 5px;
    margin-top: 30px;
}
.main-2-cont-1{
    width: 80%;
    height: auto;
    margin: 25px auto;
}
#all-users{
     width: 80%;
    margin: 30px auto;

    height: 70vh;
}
#user{

}
#all-users tr{
    width: 100%;
}
#all-users tr td,
#all-users tr th{
    text-align: center;
    padding: 7px 5px;
}
#user tr{
    width: 100%;
}

#user tr th,
#user tr td{
    padding: 10px 15px;
}
.main-4{
    width: 60%;
    margin: 20px auto 0 auto;
    min-height: calc(100vh - 100px);
}
#all-users tr:nth-child(even),
#user tr:nth-child(even){
    background: #f8f8f8;
}
#all-users tr:nth-child(odd),
#user tr:nth-child(odd){
    background: #ccc;
}
.main-2-1{
    height: 100px;
}
.main-4-cont{

    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    text-align: center;
}

.main-4-cont form{
    width: 80%;
    height: 85%;
    display: flex;
    background-color: #fafafa;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: left;
    margin: auto;
}
.main-4-cont form input{
    width: 60%;
    margin: 10px;
    padding: 7px;
}

.main-4-cont form label{
    padding: 10px;
    text-align: left;
}

.main-4-cont form input[type="submit"]{
    width: fit-content;
    padding: 15px;
    border-radius: 8px;
}

.footer, .footer-main{
    width: 100%;
    height: 100px;
    background-color: rgb(80,167,227);
    color: #fff;
}
.footer-main{
    
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
}
.footer-main-1{
    width: 30%;
    display: flex;
    justify-content: space-around;
    align-content: center;
    align-items: center;
    text-align: center;
}

.footer-main-1 img{
    height: 80px;
}

.footer-main-2{
    width: 60%;
    
}
.footer-main-2 .sm{
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
}

@media(max-width:860px){
    .header{
        flex-direction: column;
        height: fit-content;
    }
    .header-left, .header-right{
        width: 100%;
    }

    .nav{
        flex-direction: column;
    }
    .menu{
        display: block;
        border: 2px solid #fff;
    }
   .main-2,.main-3,.main-4,
   .main-2-left,.main-4-left{
                    width: 100%;
                    height: fit-content;
                    grid-template-columns: auto;
        }    
        .main-4{
            margin-top:50px;
        }
        .main-1-left, .main-cont-1{
            display: flex;
            flex-direction: column;
            width: 100%;
        }
    
       .main-1{
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
       }
       .main-cont-1{
           margin-top: 20px ;
       }
       .main-2-right{
           width: 100%;
       }
       .main-1-left img{
           width: 85%;
           display: block;
           margin: auto;
       }
       .main-list li,.users ul li{
           width: 80%;
           font-size: 0.9rem;
          margin: auto;
       }
       .footer, .footer-main{
           justify-content: center;
           height: auto;
       }
       .footer-main{
           flex-direction: column;
       }
       .footer-main-1{
           width: 100%;
       }
       .footer-main-1 img{
           display: block;
       }
       .footer-main-1 > div{
           width: fit-content;
       }
       .footer-main-2 .sm{
            margin-top: 15px ;
           flex-direction: column;
       }
       .sm > div{
        padding:10px;
       }
}